@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800&display=swap');
@import "../node_modules/bootstrap/scss/bootstrap";

//Font size

$body-font-size: 15px;
$title-font-size:50px;

//Text Colors

$body-text-color: #918DC7;
$title-text-color: #353382;

//Color

$blue: #928BF9;
$white: #FFFFFF;

//Gredient Colors

$yellow-bg: linear-gradient(89.49deg, #FFA665 0.44%, #FFCB68 99.62%);
$purple-bg: linear-gradient(129.13deg, #E67AC8 7.95%, #AA82E9 91.29%);
$blue-bg: linear-gradient(129.13deg, #00E8F7 7.95%, #329DFF 91.29%);
$green-bg: linear-gradient(129.13deg, #F6FF8A 7.95%, #00FF30 91.29%);

body{
    font-family: 'Popins', sans-serif;
    font-size: $body-font-size;
    color: $body-text-color;
}

//Custom Classes
.bg-blue{
    background-color: $blue;
}

.title{
    color: $title-text-color;
    font-size: $title-font-size;
    font-weight: 800;
    line-height: 72px;
}

p{
    line-height: 38px;
}

.btn{
    font-size: 16px;
    font-weight: 500;
    padding: 15px 43px;
    border-radius: 35px;
    transition: all 0.4s ease;

    &-yellow{
        background: $yellow-bg;
        color: $white;
        &:hover{
            box-shadow: 0px 4px 17px rgba(255,198,96,0.61) ;
            color: $white;
        }
    }
    &-outline{
        border: 2px solid $title-text-color;
        color: $title-text-color;
        padding: 14px 42px;
        &:hover{
            background-color: $title-text-color;
            color: $white;
        }
    }
}


//Navbar
li.nav-item{
    margin-right: 1rem;
}


.navbar-dark .navbar-nav .nav-link{
    font-size: 18px;
    font-weight: 600;
    color: $white;
    padding-left: 25px;
    padding-right: 25px;
    border-radius: 30px;

    &:hover, &.active{
        background-color: #A9A4FF;
    }

}


//Features Section
.features{
    margin-top: 70px;
}
.feature{
    h3{
        color: $title-text-color;
        font-size: 28px;
        font-weight: 500;
        line-height: 42px;
        padding-bottom: 20px;
    }
    &-icon{
        margin-bottom: 25px;


        i{
            font-size: 37px;
            color: $white;
            position: relative;

            &::after{
                content: '';
                position: absolute;
                width: 85px;
                height: 85px;
                border-radius: 25px;
                background: $blue-bg;
                left: 0;
                bottom: 0;
            }

            &::before{
                position: absolute;
                z-index: 99;
                left: 23px;
                bottom: 24px;
            }
        }
    }
}
.feature:nth-child(1) i::after{
    background: $purple-bg;
}
.feature:nth-child(2) i::after{
    background: $blue-bg;
}
.feature:nth-child(3) i::after{
    background: $green-bg;
}


//Courses Section
.courses h4{
    color: $title-text-color;
    font-weight: 600;
    font-size: 24px;
    margin-top: 15px;
}


//Stats
.stats{
    padding: 94px 65px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 37px;
}

.stat{
    text-align: center;
    color: $white;

    &-num{
        font-weight: 600;
        font-size: 64px;
        margin-bottom: 30px;
    }
    &-title{
        font-weight: 600;
        font-size: 24px;
    }
}


//Pricing Section
.card{
    background: #FDFDFD;
    border-radius: 20px;
    border: none;
    text-align: center;
    padding: 50px 0;

    &-body{
        padding: 0 15px;
        p{
            display: inline-block;
            padding: 7px 20px;
            font-size: 14px;
            font-weight: 700;
            text-transform: uppercase;
            color: $white;
            letter-spacing: 1px;
            border-radius: 40px;
            &.starter{
                background: $yellow-bg;
            }
            &.pro{
                background: $purple-bg;
            }
            &.single{
                background: $blue-bg;
            }
            &.family{
                background: $green-bg;
            }
        }
        h5{
            color: $title-text-color;
            font-weight: 500;
            font-size: 28px;
            margin: 14px 35px;
        }
    }
    li.list-group-item{
        color: $body-text-color;
        background-color: transparent;
    }
}


//Newsletter Section
.newsletter{
    border-radius: 30px;

    &-box{
        padding: 14px 20px;
        text-align: center;

        h2{
            color: $white;
            font-weight: 600;
            font-size: 36px;
            margin-bottom: 30px;
        }

        .btn{
            padding: 15px 60px;
        }
    }
}


